import React, { useState, useEffect } from 'react';
import CardList from '../components/CardList';
import SearchBox from '../components/SearchBox';
import Scroll from '../components/Scroll';
import './App.css';

function App() {
  const [animeFriends, setAnimeFriends] = useState([])
  const [searchfield, setSearchfield] = useState('')

  useEffect(()=> {
    fetch('https://princ09.github.io/json_server/anime.json')
      .then(response=> response.json())
      .then(users => {setAnimeFriends(users)});
    // console.log(count)
  },[]) // if you add count, only run if count changes.

  const onSearchChange = (event) => {
    setSearchfield(event.target.value)
  }

  const filteredAnimeFriends = animeFriends.filter(robot =>{
    return robot.name.toLowerCase().includes(searchfield.toLowerCase());
  })

  return !animeFriends.length ?
    <h1>Loading</h1> :
    (
      <div className='tc'>
        <h1 className='f1'>Anime Friends</h1>
        <SearchBox searchChange={onSearchChange}/>
        <Scroll>
          <CardList animeFriends={filteredAnimeFriends} />
        </Scroll>
      </div>
    );
}

export default App;